<template>
  <div class="container">
    <div class="gxbalkdeposit">
      <el-form :model="depositForm" ref="depositForm" label-width="60px" :rules="depositRules">
        <el-form-item :label="this.$t('assetScene.amount')" prop="amount">
          <div>
            <el-input
              :placeholder="this.$t('assetScene.depositamount')"
              v-model="depositForm.amount"
              class="input-with-select"
            >
              <el-select
                popper-class="input-with-select-coin"
                v-model="depositForm.coin_code"
                slot="append"
                placeholder="请选择"
                style="width:120px"
              >
                <el-option label="GXC" value="GXC"></el-option>
              </el-select>
            </el-input>
          </div>
        </el-form-item>
        <!-- <el-form-item :label="$t('loginPart.VerificationCode')" prop="vcode">
          <el-input v-model="depositForm.vcode">
            <el-button
              type="primary"
              plain
              @click="sendCode"
              :loading="smsloading"
              :disabled="smsdisabled"
              slot="append"
              style="width:120px"
            >{{VerificationCode}}</el-button>
          </el-input>
        </el-form-item>-->
        <el-form-item label-width="0px" align="center">
          <el-button
            type="primary"
            :loading="loading"
            @click="deposit"
            style="width:50%;"
          >{{this.$t("assetScene.deposit")}}</el-button>
        </el-form-item>
      </el-form>

      <div class="current">
        {{this.$t("assetScene.AccountBalance")}}
        <span>{{cur_coin_amount}} {{cur_coin_code}}</span>
      </div>

      <div class="erc20deposit-tip">
        <p>{{this.$t("assetScene.depositGxbTips")}}</p>
      </div>
    </div>
  </div>
</template>

<script>
import AssetService from '../../../api/asset'
import Util from '../../../utils/util'

import BlockCity from 'blockcity-js-sdk'

export default {
  data() {
    var validateAmount = (rule, value, callback) => {
      if (value == '')
        return callback(new Error(this.$t('assetScene.depositPlaceholder')))
      return callback()
    }
    var validateVcode = (rule, value, callback) => {
      if (!this.depositForm.order_id) {
        callback(new Error(this.$t('loginPart.VerificationCodeFormatError3')))
      }
      if (value === '') {
        callback(new Error(this.$t('loginPart.VerificationCodePlaceHolder')))
      } else if (!/\d/.test(value)) {
        callback(new Error(this.$t('loginPart.VerificationCodeFormatError1')))
      } else if (value.length != 6) {
        callback(new Error(this.$t('loginPart.VerificationCodeFormatError2')))
      } else {
        callback()
      }
    }
    return {
      loading: false,
      cur_coin_code: 'USDT',
      cur_coin_amount: '0.00',

      VerificationCode: this.$t('loginPart.sendCode'),
      smsloading: false,
      smsdisabled: false,

      depositForm: {
        order_id: '',
        amount: '',
        coin_code: 'USDT',
        vcode: ''
      },
      balances: {
        usdt: 0,
        dt: 0,
        ones: 0,
        just: 0
      },
      depositRules: {
        amount: [{ validator: validateAmount, trigger: 'blur' }],
        vcode: [{ validator: validateVcode, trigger: 'blur' }]
      }
    }
  },
  watch: {
    'depositForm.coin_code': function(val) {
      console.log(val)
      var key = val.toLowerCase()
      this.cur_coin_code = val.toUpperCase()
      this.cur_coin_amount = this.balances[key]
    }
  },

  mounted: function() {
    //action=deposit
    var coin_code = this.$route.params.coin_code
    var coin_amount = this.$route.params.coin_amount
    if (coin_code) {
      this.cur_coin_code = coin_code.toUpperCase()
      this.depositForm.coin_code = coin_code.toUpperCase()
      this.cur_coin_amount = Number(coin_amount).toFixed(4)
    }
    const action = this.$route.query.action

    this.getBalances()
  },

  methods: {
    deposit() {
      var that = this
      this.$refs.depositForm.validate(valid => {
        if (valid) {
          this.loading = true
          var amount = this.depositForm.amount
          var coin_code = this.depositForm.coin_code.toLowerCase()
          var params = {
            type: 'gxb',
            amount: amount,
            coin_code: coin_code
          }

          AssetService.predeposit(params).then(res => {
            if (res.code == 0) {
              setTimeout(
                function() {
                  this.$confirm('确认已支付?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                  })
                    .then(() => {
                      this.paymentResult(res.data.order_id)
                    })
                    .catch(() => {
                      this.paymentResult(res.data.order_id)
                    })
                }.bind(this),
                1000
              )

              BlockCity.choosePay({
                tradeNo: res.data.trade_no,
                success: function() {
                  console.log('支付成功')
                  that.paymentResult(res.data.order_id)
                },
                fail: function(error) {
                  console.log('支付失败：' + JSON.stringify(error))
                  that.paymentResult(res.data.order_id)
                },
                cancel: function() {
                  console.log('取消支付')
                  that.paymentResult(res.data.order_id)
                }
              })
            } else {
              this.$message({
                message: this.$t('message.' + res.msg),
                type: 'error',
                center: true
              })
              this.loading = false
            }
          })
        } else {
          return false
        }
      })
    },

    getBalances() {
      AssetService.balances().then(res => {
        if (res.code == 0) {
          for (var i in res.data.coins) {
            this.balances[i] = res.data.coins[i]['balances']['bqex']
              ? res.data.coins[i]['balances']['bqex']
              : '0.00'
          }
          // this.balances = res.data.coins

          console.log(this.cur_coin_code)
          var key = this.cur_coin_code.toLowerCase()
          console.log(this.balances, key)
          this.cur_coin_amount = Number(this.balances[key]).toFixed(4)
        }
      })
    },

    paymentResult(order_id) {
      var params = {
        type: 'gxb',
        order_id: order_id
      }
      AssetService.deposit(params).then(res => {
        console.log(res, 'xxxxxxx')
        if (res.code == 0) {
          this.loading = false
          this.$message({
            message: this.$t('message.deposit_success'),
            type: 'success',
            center: true
          })

          this.depositForm.order_id = ''
          this.depositForm.amount = ''
        } else {
          this.$message({
            message: this.$t('message.' + res.msg),
            type: 'error',
            center: true
          })
          this.loading = false
        }
      })
    }
  }
}
</script>

<style scoped>
.gxbalkdeposit {
  width: 96%;
  margin: 0 auto;
}

.container {
  width: 100%;
  /* height: 100vh; */
  margin-top: 45px;
  box-sizing: border-box;
  overflow: hidden;
}

.current {
  width: 100%;
  line-height: 7vh;
  margin-top: 0vh;
  font-size: 0.9rem;
  color: #888;
  text-align: right;
}
.current span {
  color: #ffc02d;
  font-size: 0.9rem;
  font-weight: 800;
}

.erc20deposit-tip {
  padding: 10px 15px;
  line-height: 22px;
  font-size: 12px;
  color: #e64c65;
}

.input-with-select .el-input-group__append {
  background-color: #fff;
}
</style>


